@charset 'utf-8';
@import './sass/normalize';
@import './sass/_mixin';

a {
  text-decoration: none;
}
body {
  font-size: 12px;
  -webkit-user-select: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: Microsoft Yahei, sans-serif;
}
a,
img {
  -webkit-touch-callout: none;
}
img,
video {
  vertical-align: bottom;
}
ul {
  list-style-type: none;
}

input {
  -webkit-tap-highlight-color: #ff8900;
  font-family: Microsoft Yahei, sans-serif;
}

html {
  height: 100%;
}
body {
  width: 750px;
  height: 100%;
  margin: 0 auto;
  background: #fff;
}

// + loading
.w-pageloading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  &:after {
    content: '';
    width: 160px; /*px*/
    height: 148px; /*px*/
    background-size: 160px 148px; /*px*/
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -58px 0 0 -75px; /*px*/
  }
  background: #fff;
  visibility: hidden;
  opacity: 0;
}

.w-pageloading-current {
  opacity: 1;
  visibility: visible;
}

#notSupported {
  position: fixed;
  z-index: 10000;
  visibility: hidden;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #333;
  color: #fff;
  font-size: 12px; /*no*/
  .logo {
    display: block;
    width: 100px; /*no*/
    height: 100px; /*no*/
    margin: 100px auto 10px; /*no*/
    // background-image: url(../../components/widget/theme/default/images/yy-logo.png);
    background-size: contain;
    background-position: center;
  }
  .title {
    display: block;
    margin: 10px; /*no*/
    text-align: center;
  }
  .desc {
    display: block;
    margin: 10px; /*no*/
    text-align: center;
  }
  .download {
    display: block;
    background: #ffdd00;
    color: #000;
    text-align: center;
    margin: 30px auto; /*no*/
    font-size: 20px; /*no*/
    width: 200px; /*no*/
    height: 45px; /*no*/
    line-height: 45px; /*no*/
    border-radius: 45px; /*no*/
  }
}
#notSupported.show {
  visibility: visible;
}
